@import url('https://rsms.me/inter/inter.css');

* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  
}

::-webkit-scrollbar-thumb {
  background-color: #8bb8e5;
  border-radius: 20px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border: 6px solid rgba(0,0,0,0.2);
}

::-webkit-scrollbar-thumb:hover {
  background-color: #336699;
}

html,
body,
#root,
main {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  scroll-snap-type: y mandatory;
  overflow-y: overlay;
  scrollbar-color: #8bb8e5 transparent;
  /* scrollbar-gutter: stable both-edges; */
  scrollbar-width: thin;
}

body {
  overflow-x: hidden;
  cursor: url('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMzIiIGhlaWdodD0iMzIiIHZpZXdCb3g9IjAgMCAzMiAzMiIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48Y2lyY2xlIGN4PSIxNiIgY3k9IjE2IiByPSIxMCIgZmlsbD0iI0U4QjA1OSIvPjwvc3ZnPg=='),
    auto;
}

main {
  background: #f0f0f0;
  display: flex;
  flex-direction: row;
}

.app-main-container {
  height: 100vh;
}

.pages {
  width: 100vw;
  height: 100vh;
  scroll-snap-align: start;
}

.canvas {
  order: 2;
  flex: 1;
  height: 100%;
}

.overlay {
  position: relative;
  order: 1;
  flex: 1;
  pointer-events: none;
}

svg {
  width: 100%;
  height: 100%;
}

.main-container {
  /* color: blue; */
  position: relative;
  /* top: -7em;
  left: -50em; */
  transform: translate(-62vw, -20vh);
  animation: scene-name-appear 2s;
}

.main-container-dark {
  /* color: blue; */
  position: relative;
  /* top: -7em;
  left: -50em; */
  transform: translate(-62vw, -20vh);
  color: #f0f0f0;
}

@keyframes scene-name-appear {
  from {opacity: 0;}
  to {opacity: 1;}
}

.modus {
  white-space: pre;
  font-family: 'Inter';
  font-size: 10.5px;
  font-weight: 500;
  letter-spacing: 0em;
  opacity: 0;
  animation: scene-name-appear 4s 1s forwards;
}

.myname {
  color: #336699;
  white-space: pre;
  font-family: 'Inter';
  font-size: 64px;
  font-weight: bold;
  letter-spacing: 0em;
  margin-top: 0em;
}

.developer {
  position: relative;
  white-space: pre;
  font-family: 'Inter';
  font-size: 36px;
  font-weight: bold;
  letter-spacing: 0em;
  margin-top: 0.1em;
  opacity: 0;
  animation: scene-name-appear 2s 1s forwards;
}

.developer-dark {
  position: relative;
  white-space: pre;
  font-family: 'Inter';
  font-size: 36px;
  font-weight: bold;
  letter-spacing: 0em;
  margin-top: 0.1em;
  opacity: 0;
  animation: scene-name-appear 2s 1s forwards;
}

.developer::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: #19334d;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.developer-dark::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: #6699cc;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.developer:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

.developer-dark:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

@keyframes creative-appear {
  from  {
          opacity: 0;
          transform: translate3d(-50px, 0, 0);
        }
  to  {
        opacity: 1;
        transform: translate3d(0, 0, 0);
      }
}

