.header {
    /* position: sticky; */
    bottom: 0vh;
    left: 4vw;
    position: fixed;
    /* width: 5vw; */
    height: 80vh;
    /* border-left: 1.5px black solid; */
    z-index: 1;
    display: flex;
    flex-direction: column-reverse;
}
.header-dark {
    /* position: sticky; */
    bottom: 0vh;
    left: 4vw;
    position: fixed;
    /* width: 5vw; */
    height: 80vh;
    /* border-left: 1.5px white solid; */
    z-index: 1;
    display: flex;
    flex-direction: column-reverse;
}

.border-line {
    width: 1.5px;
    background-color: black;
    height: 15%;
}

.border-line-dark {
    width: 1.5px;
    background-color: white;
    height: 15%;
}

.icons {
    /* background-color: #f0f0f0; */
    height: 55%;
    bottom: 15%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 14px;
    margin: 0 0 0 -7px;
}

.mail {
    /* background-color: #f0f0f0; */
    padding: 3px 0px 0px 0px;
    border-radius: 50%;
}

.mail-dark {
