}

@media only screen and (max-aspect-ratio: 8/7) {
    main {
      flex-direction: column;
    }
    .canvas {
      order: 1;
    }
    svg {
      order: 2;
      width: 100%;
      max-width: 100%;
      height: 60vh;
    }
  }
  