.aboutme-main-container {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.skill-tree {
    width: 50%;
    height: 100%;
    float: right;
}

.aboutme-text {
    position: absolute;
    top: 10vh;
    left: 10vw;
}

.aboutme-text-dark {
    position: absolute;
    top: 10vh;
    left: 10vw;
    color: #f0f0f0;
}

.aboutme-title {
    color: #336699;
    white-space: pre;
    font-family: 'Inter';
    font-size: 48px;
    font-weight: bold;
    letter-spacing: 0em;
    margin-top: 0em;
}

.aboutme-desc {
    white-space: pre-line;
    font-family: 'Inter';
    font-size: 24px;
    max-width: 60vw;
    /* font-weight: bold; */
    letter-spacing: 0em;
    margin-top: 0.3em;
}